<template>
	<section class="my-container">
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="姓名"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--列表-->
		<el-table :data="users" border highlight-current-row header-cell-class-name="table-header" v-loading="listLoading" @selection-change="selsChange" style="width: 100%;text-align: left;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="name" label="姓名" width="120" >
			</el-table-column>
			<el-table-column prop="sex" label="性别" width="100" :formatter="formatSex" >
			</el-table-column>
			<el-table-column prop="age" label="年龄" width="100" >
			</el-table-column>
			<el-table-column prop="birth" label="生日" width="120" >
			</el-table-column>
			<el-table-column prop="addr" label="地址" min-width="180" >
			</el-table-column>
			<el-table-column label="操作" width="150">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button size="mini" type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	var _this
	export default {
		name: "user",
		data() {
			return {
				listLoading: false, //表格加载
				filters: {
					name: ''
				},
				sels: [], //列表选中列
				total: 2,
				users: [{
					id: 1,
					name: 'yy',
					addr: '上海市普陀区金沙江路 1519 弄',
					age: 18,
					birth: '2000-01-02',
					sex: 1
				}, {
					id: 1,
					name: 'yy',
					addr: '上海市普陀区金沙江路 1519 弄',
					age: 18,
					birth: '2011-03-04',
					sex: 0
				}]
			}
		},
		created() {
			_this = this
			console.log('created')
		},
		methods: {
			formatSex: function(row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			getUsers: function() {

			},
			handleAdd: function() {
				_this.$router.push('/user/add')
			},
			selsChange: function(sels) {
				_this.sels = sels;
			},
			handleEdit: function() {

			},
			handleDel: function() {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					setTimeout(function() {
						_this.listLoading = false
						_this.$message({
							type: 'success',
							message: '删除成功!'
						});
					}, 2000)
				}).catch(() => {

				});
			},
			handleCurrentChange: function() {

			},
			//批量删除
			batchRemove: function() {
				var ids = _this.sels.map(item => item.id).toString();
				_this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					_this.listLoading = true;
					setTimeout(function() {
						_this.listLoading = false
						_this.$message({
							type: 'success',
							message: '删除成功!'
						});
					}, 2000)
				}).catch(() => {

				});
			}
		}
	}
</script>

